.navigation {
	@extend %container;
	background: #DCE4F0;
	padding-top: 0;
	padding-bottom: 0;
}

.navigation-list {
	@include flexbox(row, wrap, baseline, center, space-between);
	list-style-type: none;
	margin: auto;
	padding: 2ex 0 2.5ex;
}

.navigation-option {
	color: $text-secondary;
	text-decoration: none;
	padding: 0.5ex 0.5em;
	margin: 0.5ex 0.5em;
	display: block;
	background: transparent;
	border-radius: 2rem;
	font-weight: 600;
	&:hover {
		@include box-shadow(4px, 8px, $opacity-third);
		padding: 1ex 1em;
		margin: 0;
		background: $brand-secondary-alt;
		border-radius: 0.11rem;
		color: $text-secondary-inverse;
	}
	&:active {
		@include box-shadow;
	}
}